<template>
    <div>
        <el-row>
            <el-col class="search-bar">
                <el-input placeholder="请输入联系人" class="w_200" icon="search" v-model="searchWord" :on-icon-click="search"></el-input>
                <div class="m_l_20">
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="addItem" icon='plus'>新建账户</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table v-loading="listLoading" class="table" :data="tableData" border width="100%" :max-height="tableMaxHeight">
                    <el-table-column align="center" label="序号" width="80">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="account" label="账户" width="150">
                    </el-table-column>
                    <el-table-column align="center" prop="password" label="密码" width="150">
                    </el-table-column>
                    <el-table-column align="center" prop="contacts" label="联系人" width="160">
                    </el-table-column>
                    <el-table-column align="center" prop="managerStatus" label="状态" :formatter="formatStatus">
                    </el-table-column>
                    <el-table-column align="center" prop="createAt.time" label="注册时间" width="150" sortable :formatter="formatTime">
                    </el-table-column>
                    <el-table-column align="center" label="操作" width="240">
                        <template scope="scope">
                            <el-button type="info" size="small" @click="editItem(scope.$index, scope.row)" icon='edit'>编辑</el-button>
                            <el-button size="small" 
                                @click="changestatus(scope.$index, scope.row)" 
                                :type="(scope.row.managerStatus == 1)? 'info': ''" 
                                v-html="(scope.row.managerStatus == 1)? '冻结': '激活'">
                            </el-button>
                            <el-button size="small" @click="deleteItem(scope.$index, scope.row)" type="danger" icon='delete2'>删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" @current-change="handleCurrentChange" :page-size="pageSize" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>
    
        <el-dialog class="edit dialog-info" title="编辑" :visible.sync="editDialog" @close="resetFields('editForm')">
            <el-form :model="eForm" label-position label-width="110px" :rules="rules" ref="editForm">
                <el-form-item label="账户：" prop="account">
                    <el-input v-model="eForm.account"></el-input>
                </el-form-item>
                <el-form-item label="密码：" prop="password">
                    <el-input v-model="eForm.password"></el-input>
                </el-form-item>
                <el-form-item label="联系人：" prop="contacts">
                    <el-input v-model="eForm.contacts"></el-input>
                </el-form-item>

                <el-form-item label="运营信息权限：">
                    <el-checkbox-group v-model="eForm.managerPowerChild.checkList1">
                      <el-checkbox label="首页轮播"></el-checkbox>
                      <el-checkbox label="系统推送"></el-checkbox>
                      <el-checkbox label="运营文章"></el-checkbox>
                      <el-checkbox label="车型库管理"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                
                <el-form-item label="用户管理权限：">
                    <el-checkbox-group v-model="eForm.managerPowerChild.checkList2">
                      <el-checkbox label="用户信息"></el-checkbox>
                      <el-checkbox label="车东信息"></el-checkbox>
                      <el-checkbox label="车辆信息"></el-checkbox>
                      <el-checkbox label="罚单信息"></el-checkbox>
                      <el-checkbox label="用户信用"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                
                <el-form-item label="订单管理权限：">
                    <el-checkbox-group v-model="eForm.managerPowerChild.checkList3">
                      <el-checkbox label="订单"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>

                <el-form-item label="财务管理权限：">
                    <el-checkbox-group v-model="eForm.managerPowerChild.checkList4">
                      <el-checkbox label="平台账户"></el-checkbox>
                      <el-checkbox label="提现管理"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>

                <el-form-item label="系统管理权限：">
                    <el-checkbox-group v-model="eForm.managerPowerChild.checkList5">
                      <el-checkbox label="账户管理"></el-checkbox>
                      <el-checkbox label="我的账户"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>

                <el-form-item label="状态：">
                    <el-radio-group v-model="eForm.managerStatus">
                        <el-radio class="radio" :label="1">激活</el-radio>
                        <el-radio class="radio" :label="2">冻结</el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editDialog = false">取 消</el-button>
                <el-button type="primary" @click="editSave('editForm')">确 定</el-button>
            </div>
        </el-dialog>
    
        <el-dialog class="add dialog-info" title="新建账户" :visible.sync="addDialog" @close="resetFields('addForm')">
            <el-form :model="aForm" label-position label-width="110px" :rules="rules" ref="addForm">
                <el-form-item label="账户：" prop="account">
                    <el-input v-model="aForm.account"></el-input>
                </el-form-item>
                <el-form-item label="密码：" prop="password">
                    <el-input v-model="aForm.password"></el-input>
                </el-form-item>
                <el-form-item label="联系人：" prop="contacts">
                    <el-input v-model="aForm.contacts"></el-input>
                </el-form-item>
                
                <el-form-item label="运营信息权限：">
                    <el-checkbox-group v-model="aForm.checkList1">
                      <el-checkbox label="首页轮播"></el-checkbox>
                      <el-checkbox label="系统推送"></el-checkbox>
                      <el-checkbox label="运营文章"></el-checkbox>
                      <el-checkbox label="车型库管理"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                
                <el-form-item label="用户管理权限：">
                    <el-checkbox-group v-model="aForm.checkList2">
                      <el-checkbox label="用户信息"></el-checkbox>
                      <el-checkbox label="车东信息"></el-checkbox>
                      <el-checkbox label="车辆信息"></el-checkbox>
                      <el-checkbox label="罚单信息"></el-checkbox>
                      <el-checkbox label="用户信用"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>
                
                <el-form-item label="订单管理权限：">
                    <el-checkbox-group v-model="aForm.checkList3">
                      <el-checkbox label="订单"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>

                <el-form-item label="财务管理权限：">
                    <el-checkbox-group v-model="aForm.checkList4">
                      <el-checkbox label="平台账户"></el-checkbox>
                      <el-checkbox label="提现管理"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>

                <el-form-item label="系统管理权限：">
                    <el-checkbox-group v-model="aForm.checkList5">
                      <el-checkbox label="账户管理"></el-checkbox>
                      <el-checkbox label="我的账户"></el-checkbox>
                    </el-checkbox-group>
                </el-form-item>

                <el-form-item label="状态：">
                    <el-radio-group v-model="aForm.managerStatus">
                        <el-radio class="radio" :label="1">激活</el-radio>
                        <el-radio class="radio" :label="2">冻结</el-radio>
                    </el-radio-group>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addDialog = false">取 消</el-button>
                <el-button type="primary" @click="addSave('addForm')">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        var checkUserName = (rule, value, callback) => {
            var check = /^[a-zA-Z0-9_]{4,16}$/
            if (!value) {
                return callback(new Error('用户名不能为空'))
            } else if (!check.test(value)) {
                return callback(new Error('用户名由字母数字下划线组成，限6-16位'))
            } else {
                return callback()
            }
        }
        return {
            word: '',
            searchWord: '',
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            tableData: [],
            editDialog: false, //弹出框-编辑
            addDialog: false, //弹出框-新建
            // 编辑-表单
            eForm: {
                managerPowerChild: {
                    checkList1: [],
                    checkList2: [],
                    checkList3: [],
                    checkList4: [],
                    checkList5: []
                }
            },
            // 添加-表单
            aForm: {
                checkList1: [],
                checkList2: [],
                checkList3: [],
                checkList4: [],
                checkList5: []
            },
            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            // 表格距离顶部的高度
            tableTop: 0,
            // 验证规则
            rules: {
                password: [
                    {required: true, message: '内容不能为空', trigger: 'blur'},
                    {min: 4, message: '大于 4 个字符', trigger: 'blur' }],
                account: [{ validator: checkUserName, required: true, trigger: 'blur' }],
                contacts: [{required: true, message: '内容不能为空', trigger: 'blur'}]
            }
        }
    },
    props: ['API'],
    computed: {
        // 计算表格高度
        tableMaxHeight() {
            return (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0) - this.tableTop - 100
        }
    },
    methods: {
        search(ev) {
            this.word = this.searchWord
            this.currentPage = 1
            this.getList(this.word);
        },
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.word)
        },
        // 格式化
            // 格式化时间
            formatTime(row, column) {
                return this.$common.getNowTime(row.createAt && row.createAt.time)
            },
            // 格式化状态
            formatStatus(row, column) {
                return row.managerStatus == 1 ? '激活' : row.managerStatus == 2 ? '冻结' : '未知';
            },
        // 查看-编辑
        editItem(index, row) {
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "manager_id": row.id
                }
            }
            this.$common._post('manager/detail', json, this, data => {
                this.editDialog = true
                this.eForm= Object.assign({}, data.manager)
            })
        },
        // 编辑保存
        editSave(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$confirm('是否确认修改?', '提示', {})
                        .then(() => {
                            if (this.eForm.managerStatus == 2 && JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName == this.eForm.account) {
                                this.$message({ type: 'error', message: '无法冻结自己' })
                                this.eForm.managerStatus = 1
                                return
                            }
                            var json = {
                                "head": {
                                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                                },
                                "body": {
                                   "manager_id": this.eForm.id,
                                   "account": this.eForm.account,
                                   "password": this.eForm.password,
                                   "manager_status": this.eForm.managerStatus,
                                   "contacts": this.eForm.contacts,
                                   "power": {
                                        checkList1: this.eForm.managerPowerChild.checkList1,
                                        checkList2: this.eForm.managerPowerChild.checkList2,
                                        checkList3: this.eForm.managerPowerChild.checkList3,
                                        checkList4: this.eForm.managerPowerChild.checkList4,
                                        checkList5: this.eForm.managerPowerChild.checkList5,
                                   }
                                }
                            }
                            this.$common._post('manager/update', json, this, data => {
                                this.editDialog = false
                                this.$message({ type: 'success',message: data.description })
                                this.getList(this.word)
                            })
                        }).catch(() => {})
                }
            })
        },
        // 查看-添加
        addItem() {
            this.aForm = {
                account: '',
                password: '',
                contacts: '',
                managerStatus: 1,
                checkList1: [],
                checkList2: [],
                checkList3: [],
                checkList4: [],
                checkList5: []
            },
            this.addDialog = true
        },
        // 添加-保存
        addSave(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$confirm('是否确认添加?', '提示', {})
                        .then(() => {
                            var json = {
                                "head": {
                                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                                },
                                "body": {
                                   "account": this.aForm.account,
                                   "password": this.aForm.password,
                                   "manager_status": this.aForm.managerStatus,
                                   "contacts": this.aForm.contacts,
                                   "power": {
                                        checkList1: this.aForm.checkList1,
                                        checkList2: this.aForm.checkList2,
                                        checkList3: this.aForm.checkList3,
                                        checkList4: this.aForm.checkList4,
                                        checkList5: this.aForm.checkList5,
                                   }
                                }
                            }
                            this.$common._post('manager/add', json, this, data => {
                                this.addDialog = false
                                this.$message({
                                    type: 'success',
                                    message: '添加成功!'
                                })
                                this.getList()
                            })
                        }).catch(null)
                }
            })
        },
        // 删除
        deleteItem(index, row) {
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "manager_id": row.id
                }
            }
            this.$common.deleteItem(this.API + 'manager/delete', this, json)
        },
        // 冻结<->激活
        changestatus(index, row) {
            this.$confirm('是否'+ (row.managerStatus==1?"冻结":"激活") +'此账号?', '提示', {})
                .then(() => {
                    var status = row.managerStatus == 1? 2: 1
                    if (status == 2 && JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName == row.account) {
                        this.$message({ type: 'error', message: '无法冻结自己' })
                        row.managerStatus = 1
                        return
                    }
                    var json = {
                        "head": {
                            "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                            "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                        },
                        "body": {
                           "manager_id": row.id,
                           "manager_status": status
                        }
                    }
                    this.$common._post('manager/changestatus', json, this, data => {
                        this.$message({ type: 'success', message: data.description })
                        this.getList(this.word)
                    })
                }).catch(() => {})
        },
        // 重置表单
        resetFields(formName){
            this.$refs[formName].resetFields()
            this.eForm = {
                managerPowerChild: {
                    checkList1: [],
                    checkList2: [],
                    checkList3: [],
                    checkList4: [],
                    checkList5: []
                }
            }
        },
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelManager', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList(this.word);
        },
        // 获取列表
        getList(contacts = ''){
            this.listLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                    "pageIndex": this.currentPage,
                    "pageSize": this.pageSize,
                    "contacts": contacts,
                    "orderBy": this.orderBy,
                }
            }
            this.$common._post('manager/list', json, this, data => {
                this.total = data.total
                this.tableData = data.list
                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        this.getList()
        // 表格距离顶部的高度
        this.tableTop = (document.querySelector('#main table').getBoundingClientRect().top)
    }
}
</script>

<style scoped lang="scss">

</style>



