<template>
	<div>
		<el-card class="box-card content-box">
			<el-row>
				<el-col>
					<p class="content">至尊宝后台账号：<span> {{account.name}}</span></p>
				</el-col>
				<el-col>
					<p class="content">至尊宝后台密码：
		                <el-input 
		                	:type="showPassword? 'password': 'text'" 
		                	placeholder="请输入密码" 
		                	class="w_200" 
		                	icon="search" 
		                	v-model="account.password" 
		                	:on-icon-click="isShow">
		                </el-input>
					</p>
				</el-col>
				<el-col class="btn-form">
					<el-button @click="">取消</el-button>
					<el-button type="primary" @click="savePassword">确定</el-button>
				</el-col>
			</el-row>
		</el-card>
	</div>
</template>

<script>
	export default {
		data(){
			return {
				account: {
					name: JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
					password: JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword,
					id: 1 
				},
				showPassword: true
			}
		},
		props: ['API'],
		methods: {
			isShow() {
				this.showPassword = !this.showPassword
			},
			savePassword() {
                if (this.account.password.trim() !== '' && this.account.password.length >= 6) {
                    this.$confirm('是否确认修改?', '提示', {})
                        .then(() => {
    	                    var json = {
    						    "head": {
    			                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
    			                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
    						    },
    						    "body": {
    						       "manager_id": this.account.id,
    						       "password": this.account.password
    						    }
    						}
    	                    this.$common._post('manager/updatepassword', json, this, 
    	                        data => {
    	                            this.$message({
    	                                message: data.description,
    	                                type: 'success'
    	                            })
    			                    var loginInfo = {
    			                    	userName: this.account.name,
    			                    	userPassword: this.account.password
    			                    }
    	                            sessionStorage.setItem('zhizunbao_login', JSON.stringify(loginInfo))
    	                            this.showPassword = false
    	                        }
    	                    )
                        }).catch(() => {})
                } else {
                    this.$message('请输入 6 位以上密码')
                    return
                }
            }
		},
		mounted() {
			if (!sessionStorage.getItem('zhizunbao_login')) {
			    this.$message('请登录账号')
			    this.$router.push('/')
			    return
			}
		}
	}
</script>

<style scoped lang="scss">
.content-box {
	width: 500px;
	margin: 100px auto;
	padding: 10px;
	.content {
		font-size: 20px;
		line-height: 40px;
	}
	.show-password {
		margin-left: 10px;
		font-size: 20px;
	}
	.btn-form {
		margin-top: 20px;
	}
}
</style>


